*{
  box-sizing: border-box;
}
  
h1,
h2,
h3,
h4,
h5,
h6,
p,
main {
  padding: 0;
  margin: 0;
}
  
button {
  padding: 0;
  border: none;
  font: inherit;

  color: inherit;
  background: transparent;

  outline: none;
  order: 1;
}
  
input {
  outline: none;
}
  
ul {
  padding: 0;
  margin: 0;

  list-style: none;
  order: 1;
}